.quiz{
    width: 1200px;
    height: 650px;
    background-color: rgb(243, 243, 243);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media (max-width: 1280px){
    .quiz{
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        position: fixed;
    }
}

#step0{display: none;}